@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap);
*{
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: linear 450ms ease-in-out;
}

.container{
  text-align: center;
  padding: 20px 20%;
  position: relative;
  transition: all 400ms ease-in-out
}

.light-mode{
  background-color: #F4F4F4;
  color: #121212;
}

.dark-mode{
  background-color: #212121;
  color: #FFF;
}

.light-mode a{
  color: #121212;
}

.dark-mode a{
  color: #F4F4F4;
}

a{
  text-decoration: none;
}

h2{
  border-bottom: 2px solid #121212;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.dark-mode h2{
  border-bottom: 2px solid #FFF;
}

nav{
  position: relative;
}

nav h1{
  font-size: 1.6em;
  margin: 10px;
  font-family: 'Montserrat', sans-serif;
}

.resume-header{
  margin-top: 25px;
}

.resume-header img{
  width: 90%;
}

.resume{
  text-align: left;
  font-size: 0.9em;
  padding: 30px 50px;
  display: flex;
  padding-bottom: 100px;
  justify-content: space-between;
}

ul{
  padding-left: 15px;
}

.resume-content-dark{
  margin: -10px auto;
  margin-bottom: 25px;
  width: 90%;
  background-color: #333;
}

.resume-content-light{
  margin: -10px auto;
  margin-bottom: 25px;
  width: 90%;
  background-color: #FFF;
}

.small-container a{
  color: #0275aa;
  text-decoration: underline;
}

.dark-mode .small-container ul li{
  color: #DDD;
}

.small-container span{
  color: #FC7575;
}

.right{
  width: 320px;
}

.download-resume{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0px 47px 50px auto;
}

.download-resume span{
  font-family: 'Montserrat', sans-serif;
}

.button-resume{
  border: none;
  padding: 10px;
  font-family: 'Montserrat', sans-serif;
  border-radius: 5px;
  margin-left: 10px;
  background-color: #40E0D0;
}
.button-resume:hover{
  background-color: aqua;
}




.footer{
  background-color: #121212;
  color: #F4F4F4;
  text-align: center;
  padding: 25px;
  padding-bottom: 15px;
}

.footer img{
  width: 175px;
}

.heart{
  color: #FF0000;
}

.twitter, .github, .linkedin {
  font-size: 2em;
  padding: 5px;
}

.github{
  color: #F4F4F4;
}

.twitter{
  color: #00ACEE;
  position: relative;
  z-index: 2;
}

.twitter__{
  position: relative;
}

.twitter__:before{
  position: absolute;
  content: "";
  width: 15px;
  height: 15px;
  background-color: #F4F4F4;
  z-index: 1;
  top: -6px;
  left: 8.5px;
}

.linkedin{
  color: #0E76A8;
  position: relative;
  z-index: 2;
}

.linkedin__{
  position: relative;
}

.linkedin__:before{
  position: absolute;
  content: "";
  width: 15px;
  height: 15px;
  background-color: #F4F4F4;
  z-index: 1;
  top: -6px;
  left: 8.5px;
}

.toggles{
  margin: 0px auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-top: 5px;
}
  
.toggle-checkbox {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
    
.toggle-slot {
  position: relative;
  height: 1em;
  width: 1.8em;
  border: 2px solid #121212;
  border-radius: 10em;
  background-color: #FFFFFF;
  transition: background-color 250ms;
}
    
.toggle-checkbox:checked ~ .toggle-slot {
  background-color: #212121;
  border: 2px solid #F4F4F4;
}
    
.toggle-button {
  transform: translate(0.8em, -0.15em);
  position: absolute;
  height: 1em;
  width: 1em;
  border-radius: 50%;
  background-color: #212121;
  box-shadow: inset 0px 0px 0px 0.1em #121212;
  transition: background-color 250ms, border-color 250ms, transform 500ms;
}
    
.toggle-checkbox:checked ~ .toggle-slot .toggle-button {
  background-color: #212121;
  box-shadow: inset 0px 0px 0px 0.75em white;
  transform: translate(-0.3em, -0.15em);
}
    
.sun-icon {
  position: absolute;
  height: 0.5em;
  width: 0.5em;
  color: #000;
}
    
.sun-icon-wrapper {
  position: absolute;
  height: 1em;
  width: 1em;
  opacity: 1;
  transform: translate(-0.35em, 0.08em) rotate(2deg);
  transform-origin: 50% 50%;
  transition: opacity 150ms, transform 500ms cubic-bezier(.26,2,.46,.71);
}
    
.toggle-checkbox:checked ~ .toggle-slot .sun-icon-wrapper {
  opacity: 0;
  transform: translate(-0.35em, 0.08em) rotate(0deg);
}
    
.moon-icon {
  position: absolute;
  height: 0.5em;
  width: 0.5em;
  color: white;
}
    
.moon-icon-wrapper {
  position: absolute;
  height: 1em;
  width: 1em;
  opacity: 0;
  transform: translate(0.45em, 0.15em) rotate(0deg);
  transform-origin: 50% 50%;
  transition: opacity 150ms, transform 500ms cubic-bezier(.26,2.5,.46,.71);
}
    
.toggle-checkbox:checked ~ .toggle-slot .moon-icon-wrapper {
  opacity: 1;
  transform: translate(0.45em, 0.15em) rotate(-15deg);
}
